body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  --c-1: #b8efd6;
  --c-2: #bbb8ef;
  --c-3: #efb8d1;
  --c-4: #edefb8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.parent {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  position: absolute;
  inset: 0;

  color: var(--c-3);
  font-size: 400px;
}

.div1 {
  grid-area: 1 / 1 / 4 / 3;
  background: var(--c-4);
}
.div2 {
  grid-area: 1 / 3 / 3 / 5;
  background: var(--c-1);
  color: var(--c-2);
}
.div3 {
  grid-area: 3 / 3 / 5 / 5;
  background: var(--c-4);
}
.div4 {
  grid-area: 4 / 1 / 5 / 3;
  background: var(--c-1);
}
.div5 {
  grid-area: 1 / 5 / 2 / 6;
  background: var(--c-2);
}
.div6 {
  grid-area: 4 / 5 / 5 / 6;
  background: var(--c-1);
}
.div7 {
  grid-area: 2 / 5 / 4 / 6;
  background: var(--c-4);
  font-size: 40px;
  overflow: hidden;
}
